
"use client";


import Head from 'next/head';
import FeaturedCollections from './components/FeaturedCollections';
import WhyChooseUs from './components/WhyChooseUs';
import Testimonials from './components/Testimonials';
import InstagramFeed from './components/InstagramFeed';

import dynamic from 'next/dynamic';
const MotionSection = dynamic(
  () => import('framer-motion').then(mod => mod.motion.section),
  { ssr: false }
);

import MistLottie from './components/MistLottie';
import HeroMedia from './components/HeroMedia';

export default function Home() {
  return (
    <>
      <Head>
        <title>SBK FRAGRANCESsa - Bringing to life the fragrance you love</title>
        <meta name="description" content="Discover scents that define your style" />
      </Head>
      <div className="relative h-[80vh] w-full flex items-stretch justify-center">
        <MistLottie />
        <HeroMedia />
        <div className="absolute inset-0 flex flex-col items-center justify-center z-10 text-center text-white pointer-events-none">
          <h1 className="text-4xl md:text-6xl font-bold drop-shadow-lg mb-4">Bringing to life the fragrance you love</h1>
          <p className="text-lg md:text-2xl mb-6">Discover scents that define your style</p>
          <div className="flex flex-col md:flex-row gap-4 justify-center mt-2 pointer-events-auto">
            <a href="/shop" className="px-8 py-3 rounded-full bg-green-600 hover:bg-green-700 text-white font-semibold shadow-lg transition hover:scale-105">Shop Now</a>
            <a href="/collections" className="px-8 py-3 rounded-full bg-white/80 hover:bg-white text-green-700 font-semibold shadow-lg transition hover:scale-105">Explore Collections</a>
          </div>
        </div>
  </div>
      <FeaturedCollections />
      <WhyChooseUs />
      <Testimonials />
      <InstagramFeed />
    </>
  );
}
